export class EditMenu {
    constructor(i18nService, messagingService, isLocked) {
        this.id = "editMenu";
        this._i18nService = i18nService;
        this._messagingService = messagingService;
        this._isLocked = isLocked;
    }
    get label() {
        return this.localize("edit");
    }
    get items() {
        return [
            this.undo,
            this.redo,
            this.separator,
            this.cut,
            this.copy,
            this.paste,
            this.separator,
            this.selectAll,
            this.separator,
            this.copyUsername,
            this.copyPassword,
            this.copyVerificationCodeTotp,
        ];
    }
    get undo() {
        return {
            id: "undo",
            label: this.localize("undo"),
            role: "undo",
        };
    }
    get redo() {
        return {
            id: "redo",
            label: this.localize("redo"),
            role: "redo",
        };
    }
    get separator() {
        return { type: "separator" };
    }
    get cut() {
        return {
            id: "cut",
            label: this.localize("cut"),
            role: "cut",
        };
    }
    get copy() {
        return {
            id: "copy",
            label: this.localize("copy"),
            role: "copy",
        };
    }
    get paste() {
        return {
            id: "paste",
            label: this.localize("paste"),
            role: "paste",
        };
    }
    get selectAll() {
        return {
            id: "selectAll",
            label: this.localize("selectAll"),
            role: "selectAll",
        };
    }
    get copyUsername() {
        return {
            label: this.localize("copyUsername"),
            id: "copyUsername",
            click: () => this.sendMessage("copyUsername"),
            accelerator: "CmdOrCtrl+U",
            enabled: !this._isLocked,
        };
    }
    get copyPassword() {
        return {
            label: this.localize("copyPassword"),
            id: "copyPassword",
            click: () => this.sendMessage("copyPassword"),
            accelerator: "CmdOrCtrl+P",
            enabled: !this._isLocked,
        };
    }
    get copyVerificationCodeTotp() {
        return {
            label: this.localize("copyVerificationCodeTotp"),
            id: "copyTotp",
            click: () => this.sendMessage("copyTotp"),
            accelerator: "CmdOrCtrl+T",
            enabled: !this._isLocked,
        };
    }
    localize(s) {
        return this._i18nService.t(s);
    }
    sendMessage(message) {
        this._messagingService.send(message);
    }
}
