import { Account as BaseAccount, AccountSettings as BaseAccountSettings, } from "jslib-common/models/domain/account";
export class AccountSettings extends BaseAccountSettings {
    constructor() {
        super(...arguments);
        this.vaultTimeout = -1; // On Restart
    }
}
export class Account extends BaseAccount {
    constructor(init) {
        super(init);
        this.settings = new AccountSettings();
        Object.assign(this.settings, Object.assign(Object.assign({}, new AccountSettings()), this.settings));
    }
}
