export class ViewMenu {
    constructor(i18nService, messagingService, isLocked) {
        this._i18nService = i18nService;
        this._messagingService = messagingService;
        this._isLocked = isLocked;
    }
    get label() {
        return this.localize("view");
    }
    get items() {
        return [
            this.searchVault,
            this.separator,
            this.generator,
            this.passwordHistory,
            this.separator,
            this.zoomIn,
            this.zoomOut,
            this.resetZoom,
            this.separator,
            this.toggleFullscreen,
            this.separator,
            this.reload,
            this.toggleDevTools,
        ];
    }
    get searchVault() {
        return {
            id: "searchVault",
            label: this.localize("searchVault"),
            click: () => this.sendMessage("focusSearch"),
            accelerator: "CmdOrCtrl+F",
            enabled: !this._isLocked,
        };
    }
    get separator() {
        return { type: "separator" };
    }
    get generator() {
        return {
            id: "generator",
            label: this.localize("generator"),
            click: () => this.sendMessage("openGenerator"),
            accelerator: "CmdOrCtrl+G",
            enabled: !this._isLocked,
        };
    }
    get passwordHistory() {
        return {
            id: "passwordHistory",
            label: this.localize("passwordHistory"),
            click: () => this.sendMessage("openPasswordHistory"),
            enabled: !this._isLocked,
        };
    }
    get zoomIn() {
        return {
            id: "zoomIn",
            label: this.localize("zoomIn"),
            role: "zoomIn",
            accelerator: "CmdOrCtrl+=",
        };
    }
    get zoomOut() {
        return {
            id: "zoomOut",
            label: this.localize("zoomOut"),
            role: "zoomOut",
            accelerator: "CmdOrCtrl+-",
        };
    }
    get resetZoom() {
        return {
            id: "resetZoom",
            label: this.localize("resetZoom"),
            role: "resetZoom",
            accelerator: "CmdOrCtrl+0",
        };
    }
    get toggleFullscreen() {
        return {
            id: "toggleFullScreen",
            label: this.localize("toggleFullScreen"),
            role: "togglefullscreen",
        };
    }
    get reload() {
        return {
            id: "reload",
            label: this.localize("reload"),
            role: "forceReload",
        };
    }
    get toggleDevTools() {
        return {
            id: "toggleDevTools",
            label: this.localize("toggleDevTools"),
            role: "toggleDevTools",
            accelerator: "F12",
        };
    }
    localize(s) {
        return this._i18nService.t(s);
    }
    sendMessage(message) {
        this._messagingService.send(message);
    }
}
